import * as THREE from 'three'
import { useEffect, useRef, useState } from 'react'
import { Canvas, useThree, useFrame } from '@react-three/fiber'
import { useTexture, useCursor, MeshReflectorMaterial, Image, Text, Environment } from '@react-three/drei'
import { useRoute, useLocation } from 'wouter'
import { easing } from 'maath'
import getUuid from 'uuid-by-string'
import { FlyControls } from '@react-three/drei'

const GOLDENRATIO = 1.61803398875

function Background() {
  const { viewport } = useThree()
  const texture = useTexture('https://res.cloudinary.com/drah2frvl/image/upload/v1690616152/amadine/ren7e5ctcp3o1y9xbbvd.jpg')

  const aspect = viewport.width / viewport.height
  const width = viewport.width > viewport.height ? 15 * aspect : 15
  const height = viewport.width > viewport.height ? 15 : 15 / aspect

  return (
    <mesh position={[0, 0, -20]}>
      <sphereGeometry args={[500, 60, 40]} />
      <meshBasicMaterial map={texture} side={THREE.BackSide} />
    </mesh>
  )
}

export const App = ({ images }) => (
  <Canvas dpr={[1, 1.5]} camera={{ fov: 70, position: [0, 2, 15] }}>
    {/* <color attach="background" args={['#191920']} /> */}
    <color attach="background" args={['#191920']} />
    <fog attach="fog" args={['#191920', 0, 15]} />
    <group position={[0, -0.5, 0]}>
      <Frames images={images} />
      <Background />
      <mesh rotation={[-Math.PI / 2, 0, 0]}>
        <planeGeometry args={[50, 50]} />
        <MeshReflectorMaterial
          blur={[300, 100]}
          resolution={2048}
          mixBlur={1}
          mixStrength={80}
          roughness={1}
          depthScale={1.2}
          minDepthThreshold={0.4}
          maxDepthThreshold={1.4}
          // color="#050505"
          color="#050505"
          metalness={0.5}
        />
      </mesh>
    </group>
    <Environment preset="city" />
    {/* <FlyControls /> */}
  </Canvas>
)

function Frames({ images, q = new THREE.Quaternion(), p = new THREE.Vector3() }) {
  const ref = useRef()
  const clicked = useRef()
  const [, params] = useRoute('/item/:id')
  const [, setLocation] = useLocation()
  const firstImageUrl = 'https://res.cloudinary.com/drah2frvl/image/upload/v1690604503/amadine/r9debu5v63nyh3cosph8.png' // add this line

  useEffect(() => {
    clicked.current = ref.current.getObjectByName(params?.id)
    if (clicked.current) {
      clicked.current.parent.updateWorldMatrix(true, true)
      if (clicked.current.name === getUuid(firstImageUrl)) {
        // Check if it's the first image
        clicked.current.parent.localToWorld(p.set(0, 1, 1.25)) // Adjust the target position for the first image
      } else {
        clicked.current.parent.localToWorld(p.set(0, GOLDENRATIO / 2, 1.25)) // Original target position for other images
      }
      clicked.current.parent.getWorldQuaternion(q)
    } else {
      p.set(0, 0, 5.5)
      q.identity()
    }
  })
  useFrame((state, dt) => {
    easing.damp3(state.camera.position, p, 0.4, dt)
    easing.dampQ(state.camera.quaternion, q, 0.4, dt)
  })
  return (
    <group
      ref={ref}
      onClick={(e) => (e.stopPropagation(), setLocation(clicked.current === e.object ? '/' : '/item/' + e.object.name))}
      onPointerMissed={() => setLocation('/')}>
      {images.map((props) => <Frame key={props.url} {...props} /> /* prettier-ignore */)}
    </group>
  )
}

function Frame({ url, title, description, c = new THREE.Color(), ...props }) {
  const image = useRef()
  const frame = useRef()
  const [, params] = useRoute('/item/:id')
  const [hovered, hover] = useState(false)
  const [rnd] = useState(() => Math.random())
  const name = getUuid(url)
  const isActive = params?.id === name

  const firstImageUrl = 'https://res.cloudinary.com/drah2frvl/image/upload/v1690604503/amadine/r9debu5v63nyh3cosph8.png' // add this line
  const firstImageScale = [1.2, 1.2 * GOLDENRATIO, 0.05] // bigger scale for the first image
  const defaultScale = [1, GOLDENRATIO, 0.05] // default scale for other images
  const firstImagePosition = [0, 1, 0] // slightly lifted position for the first image
  const defaultPosition = [0, GOLDENRATIO / 2, 0] // default position for other images
  const defaultTextPosition = [0.55, GOLDENRATIO, 0]
  const firstImageTextPosition = [0.7, GOLDENRATIO, 0]

  useCursor(hovered)
  useFrame((state, dt) => {
    if (url !== firstImageUrl) {
      // add this condition
      image.current.material.zoom = 2 + Math.sin(rnd * 10000 + state.clock.elapsedTime / 3) / 2
    }

    easing.damp3(image.current.scale, [0.85 * (!isActive && hovered ? 0.85 : 1), 0.9 * (!isActive && hovered ? 0.905 : 1), 1], 0.1, dt)
    easing.dampC(frame.current.material.color, hovered ? 'orange' : 'white', 0.1, dt)
  })
  return (
    <group {...props}>
      <mesh
        name={name}
        onPointerOver={(e) => (e.stopPropagation(), hover(true))}
        onPointerOut={() => hover(false)}
        scale={url === firstImageUrl ? firstImageScale : defaultScale} // use the first image scale if the url matches
        position={url === firstImageUrl ? firstImagePosition : defaultPosition}>
        <boxGeometry />
        <meshStandardMaterial color="#151515" metalness={0.5} roughness={0.5} envMapIntensity={2} />
        <mesh ref={frame} raycast={() => null} scale={[0.9, 0.93, 0.9]} position={[0, 0, 0.2]}>
          <boxGeometry />
          <meshBasicMaterial toneMapped={false} fog={false} />
        </mesh>
        <Image raycast={() => null} ref={image} position={[0, 0, 0.7]} url={url} />
      </mesh>
      <Text maxWidth={0.2} anchorX="left" anchorY="top" position={url === firstImageUrl ? firstImageTextPosition : defaultTextPosition} fontSize={0.055}>
        {title} {/* Display title */}
      </Text>
      <Text maxWidth={0.2} anchorX="left" anchorY="bottom" position={url === firstImageUrl ? firstImageTextPosition : defaultTextPosition} fontSize={0.025}>
        {description} {/* Display description */}
      </Text>
    </group>
  )
}
